@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
}

/* titre */
h1, h2, h3{
  text-align: center;
}
h1{
  font-size:3em;
    margin:1%;
}
h2{
  font-size:2.5em;
}
h3{
  font-size:2em;
  margin: 0.5em;
}
h4{
  -webkit-text-decoration: underline #373528;
          text-decoration: underline #373528;
  font-size:1.5em;
  margin: 0.3em;
}
h5{
  font-size:1.3em;
  margin: 0.1em;
}

/* image */
img{
  width:100%;
  height:100%;
  border-radius:8px;
}

/* lien */
a{
  text-decoration: none;
  color:#373528;
}

/* liste */
li{
  list-style: none;
}
ul{
  padding: 0;
}

/* paragraphe */
p{
  margin:0.1em 0;
}

/* contenu */
.page {
  background-image: url(/paris-events/static/media/img-paris-1-bg.a0f888c4.jpg);
  min-height: 100vh;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  font-family: 'Open Sans Condensed', sans-serif;
  color:#373528;
}
.main-content{
margin:auto;
max-width:80%;
background-color: rgba(255, 255, 255, 0.7);
min-height: 75vh;
}

/*style général pour le component ErrorData.js*/
.page-error-API p{
    text-align:center;
    color:red;
}

/*Style pour smartphone */
@media screen and (max-width:480px) {
    .page-error-API p{
        font-size:1.5em;
        margin:1em;
    }
}

/*Style pour tablette */
@media screen and (min-width:480px) and (max-width:959px) {
    .page-error-API p{
        font-size:2.5em;
        margin:1.5em;
    }
}

/*Style pour ordinateur Ecran taille normal*/
@media screen and (min-width:960px) {
    .page-error-API p{
        font-size:3.5em;
        margin:1.8em;
    }
}
/*style général pour le dernier evenement*/
.none{
    display:none;
}
.event-content ul{
    font-size: 1.5em
}

.event-content img{
    width: 100%;
    margin: auto;
    padding: 0 2%;
}
.event-content ul{
    margin-top: 0px;
}

  /*Style pour ordinateur Ecran taille normal*/
@media screen and (min-width:960px) {
    .event-content {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    
    .event-content img {
        display:block;
        width:50%;
    }
    .event-content ul {
        display:block;
        width:50%;
    }
  }
/*Style pour smartphone */
.detail-card{
    position: absolute;
    background-color: rgb(255, 255, 255);
    border-radius: 8px;
    width: 90%;
    padding: 3%;
    box-shadow: 10px 5px 5px #373528;
    top: 10%;
    right: 5%;
    z-index: 5;
}
.overlay { /* background qui recouvre le fond */
    position: fixed; 
    width: 100%; 
    height: 100%; 
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5); 
    z-index: 2; 
}

/* button close-detail*/
.close-detail{
    margin: 1em auto;
    display:block;
    text-align:center;
    padding: 0.7em;
    background-color: rgba(255, 255, 255, 0.9);
    border: none;
    border-radius: 8px;
    box-shadow: 1px 1px 1px #373528;
}
.close-detail:hover {
    color:white;
    font-weight: bold;
    background-color: #373528;
    border-radius: 5px;
    box-shadow: none;
}

/*Style pour ordinateur Ecran taille normal*/
@media screen and (min-width:960px) {
.event-detail{
    display:flex;
    justify-content:space-between; 
}
.event-detail img{
width:40%;
}
.event-description{
    width:38%;
    padding:1%;
}
.event-infos{
    background-color: rgba(0, 0, 0, 0.3);
    width:20%;
    padding:1%;
    border-radius: 8px;
}
}
/*STYLE SUR LE BUTTON FAVORIS*/
.favorite-button{
    margin: 1em auto;
    display:block;
    text-align:center;
    padding: 0.7em;
    background-color: rgba(255, 255, 255, 0.9);
    border: none;
}

.favorite-button:hover {
    color:white;
    font-weight: bold;
    background-color: #373528;
    border-radius: 8px;
}

.not-found h1{
    color: red;
    text-transform: uppercase;
}
.not-found p{
    color: red;
    text-align:center
}
/*style général pour les evenements recherchés*/
.card{
    padding: 1% 5%;
    background-color: rgba(255, 255, 255, 0.5);
    margin: 3%;
    border-radius: 20px;
}
.card img{
    width: 100%;
    margin: auto;
    padding: 0 2%;
}
.card ul{
    margin: 0 auto;
}

/*Style pour ordinateur Ecran taille normal*/
@media screen and (min-width:960px) {

    .card-content{
        display:flex;
        justify-content: space-around;
        align-items: center;
    }
    .card img{
        width: 50%;
        padding: 1%;
    }
    .card ul{
        width: 50%;
        padding: 1%;
        font-size:1.5em;
    }
    
}

.searchTermInput{
    border:none;
    height:50px;
}
.error-term p{
    color:red;
    text-align:center;
}
.search-button {
    background-color: rgba(255, 255, 255, 0.5);
    border: none;
    border-radius: 3px;
}

.search-button:hover {
    background-color: rgba(255, 255, 255, 0.7);
}
/*Style pour smartphone */
@media screen and (max-width:480px) {
    .searchTermInput{
        font-size:1.5em;
        width:98%;
        margin:1%;
        text-align:center
    }
    .search-button{
        font-size:1.5em;
        width:100%;
        padding:1%;
        margin:auto;
    }
    .error-term p{
        font-size:1.5em;
        padding:1.3%;
    }
}

/*Style pour tablette */
@media screen and (min-width:480px) and (max-width:959px) {
    .search{
        display:flex;
        justify-content: space-around;
    }
    .searchTermInput{
        font-size:1.8em;
        width:65%;
        margin:1%;
        border:1px solid black;
    }
    .search-button{
        font-size:1.3em;
        width:30%;
        margin:auto;
        border:1px solid black;
        padding:1.7%;
    }
    .error-term p{
        font-size:2.3em;
        padding:1.5%;
    }
}

/*Style pour ordinateur Ecran taille normal*/
@media screen and (min-width:960px) {
    .search{
        display:flex;
        justify-content: space-around;
    }
    .searchTermInput{
        font-size:1.8em;
        width:80%;
        margin:1%;
    }
    .search-button{
        font-size:1.8em;
        width:15%;
        margin:auto;
        padding:0.5%;
    }
    .error-term p{
        font-size:2.7em;
        padding:1.5%;
    }
}
/*style général pour navbar*/
nav {
    padding: 1px;
    background-color: rgba(255, 255, 255, 0.1);
}

nav ul {
    font-size:1.5em;
    font-weight: bold;
    text-transform: uppercase;
}

nav li a, nav li a:visited {
    color:white;
    text-shadow: #373528 5px 0 10px;
    font-weight: bold;
    padding: 0.2em;
  }

nav li a:hover {
    color:white;
    font-weight: bold;
    background-color: #373528;
    border-radius: 5px;
}
nav ul li{
    padding:1%;
}

/*Style pour smartphone */
@media screen and (max-width:480px) {
    nav ul {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin:0
    }
    nav ul li{
        padding:1%;
    }
  }

  /*Style pour tablette */
@media screen and (min-width:480px) and (max-width:959px) {
    nav ul {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
  }

  /*Style pour ordinateur Ecran taille normal*/
@media screen and (min-width:960px) {
    nav ul {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
  }
